import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Moment from 'react-moment';
import {Link} from 'react-router-dom';
import { connect } from 'react-redux';
import {addLike,deletePost,removeLike} from '../../actions/post';


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { icon } from '@fortawesome/fontawesome-svg-core/import.macro';

const PostItem = ({
    addLike,
    removeLike,
    deletePost,
    auth,
    showActions,
    post:{_id,text,name,avatar,user,likes,comments,date}}
    ) => {
  
  
    return (
            <div className="post bg-white p-1 my-1">
                <div>
                    <Link to={`/profile/${user}`}>
                        <img
                            className="round-img"
                            src={avatar}
                            alt="avatar"
                        />
                        <h4>{name}</h4>
                    </Link>
                </div>
                <div>
                    <p className="my-1">
                        {text}
                    </p>
                    <p className="post-date">
                        <strong>Posted on</strong> 
                        <Moment format="YYYY/MM/DD">{date}</Moment>
                    </p>

                    {showActions && (
                        <Fragment>
                            <button onClick={e => addLike(_id)} type="button" className="btn btn-light">
                                <FontAwesomeIcon style={{marginRight:'10px'}} icon={icon({name:'thumbs-up',style:'solid'})} /> 
                                <span> {' '} {likes.length > 0 && (<span className='comment-count'>{comments.length}</span>
                                )} </span>
                            </button>

                            <button onClick={ e => removeLike(_id)} type="button" className="btn btn-light">
                                <FontAwesomeIcon style={{marginRight:'10px'}} icon={icon({name:'thumbs-down',style:'solid'})} /> 
                            </button>

                            <Link to={`/posts/${_id}`} className="btn btn-primary">
                                Discussion {' '} {comments.length > 0 && (<span className='comment-count'>{comments.length}</span>
                                )} 
                            </Link>

                            {!auth.loading && user === auth.user._id && (
                                <button onClick={e => deletePost(_id)} type="button" className="btn btn-danger">
                                    <FontAwesomeIcon style={{marginRight:'10px'}} icon={icon({name:'times',style:'solid'})} />
                                </button>
                            )}
                        </Fragment>
                    )}              

                </div>
            </div> 
  )
}

PostItem.defaultProps= {
    showActions:true
}

PostItem.propTypes = {
    post: PropTypes.object.isRequired,
    auth: PropTypes.object.isRequired,
    addLike: PropTypes.func.isRequired,
    removeLike: PropTypes.func.isRequired,
    deletePost: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({
    auth: state.auth
  });

export default connect(
    mapStateToProps,
    {addLike,removeLike,deletePost}
)(PostItem)